:root {
  --notebook-shared-link-copy-icon-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  --notebook-shared-badge-copy-icon-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
