	font-size: 24px;
	font-style: normal;
	font-weight: 600;
}

.notebook-shared-modal .jp-Dialog-content {
    min-height: 585px;
    width: 515px;
}

.notebook-shared-modal .jp-Dialog-body {
	overflow: hidden;
}

.notebook-shared-modal .jp-Dialog-footer button {
	color: #06262D !important;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
}
